import { ChatBox } from './ChatBox';
import { Logger } from '../services/logger';

//  content script 
Logger.info('Content script ');

let chatBox = null;

//  DOM
function checkChatBoxDOM() {
  try {
    //  chatBox 
    if (!chatBox) {
      Logger.info('');
      return;
    }

    //  container
    if (!chatBox.container) {
      Logger.info('');
      return;
    }

    //  container 
    if (!document.body.contains(chatBox.container)) {
      Logger.info('');
      return;
    }

    // 
    const status = {
      exists: true,
      inDocument: true,
      visible: chatBox.visible,
      display: chatBox.container.style.display
    };

    // 
    const dimensions = {
      offsetWidth: chatBox.container.offsetWidth,
      offsetHeight: chatBox.container.offsetHeight,
      clientWidth: chatBox.container.clientWidth,
      clientHeight: chatBox.container.clientHeight
    };

    // 
    const position = {
      top: chatBox.container.style.top,
      right: chatBox.container.style.right
    };

    // 
    Logger.info('', {
      ...status,
      dimensions,
      position,
      styles: {
        backgroundColor: chatBox.container.style.backgroundColor,
        zIndex: chatBox.container.style.zIndex
      }
    });

  } catch (error) {
    Logger.error('', error);
  }
}

// 
async function initChatBox() {
  try {
    if (!chatBox) {
      Logger.info('');
      chatBox = new ChatBox();
      
      //  DOM 
      await new Promise(requestAnimationFrame);
      
      // 
      checkChatBoxDOM();
      
      Logger.info('');
    }
  } catch (error) {
    Logger.error('', error);
  }
}

//  background 
chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
  try {
    Logger.info('', request);
    
    //  ping 
    if (request.action === 'ping') {
      Logger.info(' ping ');
      sendResponse({ status: 'ok' });
      return true;
    }
    
    // 
    if (request.action === 'toggleChatBox') {
      if (!chatBox) {
        initChatBox();
      }
      chatBox.toggle();
      //  DOM
      checkChatBoxDOM();
      
      Logger.info('', { visible: chatBox.visible });
      sendResponse({ success: true });
      return true;
    }
  } catch (error) {
    Logger.error('', error);
    sendResponse({ success: false, error: error.message });
  }
  return true;
});

// 
document.addEventListener('DOMContentLoaded', () => {
  Logger.info('');
  initChatBox();
});

//  content script 
Logger.info('Content script '); 